<template>
  <div class="reply">
    <div class="font_16 black font_bold">{{ con.user_info.mobile.replace(/(\d{3})\d{4}(\d{4})/, '$1****$2') }}</div>
    <div class="">{{ con.content }}</div>
    <div class="h_justify light font_12 margin_b_15">
      <div class="">{{ con.created_at }}</div>
      <div class="cursor reply_btn" v-if="type !== 'reply'" @click="$parent.openReply(con.user_info.mobile, no)">
        <img class="width_15 img_middle reply_disabled" :src="$parent.reply_disabled">
        <img class="width_15 img_middle reply_normal" :src="$parent.reply_normal">
        <span>回复</span>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'comment',
  props: {
    con: {
      type: Object
    },
    type: {
      type: String
    },
    no: {
      type: Number
    }
  }
}
</script>

<style type="text/css">
  .reply_btn .reply_normal,.reply_btn:hover .reply_disabled{
    display: none;
  }
  .reply_btn:hover .reply_normal{
    display: inline;
  }
</style>



