<template>
  <div class="comment padding_r_50 lineheight_2">
    <div class="comment_block">
      <div class="padding_v_20 border_bottom comment_list" v-for="(d,k) in commentData">
        <div class="flex">
          <div class="width_50">
            <img class="circle" :src="d.user_info.head_logo||img">
          </div>
          <div class="padding_l_15 flex_1">
            <ccontent :con="d" :no="k"></ccontent>
            <div class="bg_light padding_10 margin_b_15" v-if="d.children_comment.length">
              <div class="flex margin_b_15" v-for="r in d.children_comment">
                <div class="width_50">
                  <img class="circle" :src="r.user_info.head_logo||img">
                </div>
                <div class="padding_l_15 flex_1">
                  <ccontent :con="r" type="reply"></ccontent>
                </div>
              </div>
            </div>
            <div v-if="k===edit">
              <div class="border circle_5 margin_b_15" style="height:120px">
                <textarea class="padding_15 all_height" placeholder="请输入评论" v-model="inputReply"></textarea>
              </div>
              <div class="row">
                <button class="btn_blue pull_right width_100 padding_v_5" :disabled="!inputReply" @click="doComment">回复</button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="pager row padding_v_20">
      <div class="pull_right light">
        <div class="pager_btn" :disabled="nowPage === 1" @click="goPage(nowPage-1)"><</div>
        <div :class="['pager_btn', {black: nowPage===p}]" v-for="p in Math.ceil(comments.length / perNum)" @click="goPage(p)">{{ p }}</div>
        <div class="pager_btn" :disabled="nowPage === Math.ceil(comments.length / perNum)" @click="goPage(nowPage+1)">></div>
      </div>
    </div>
    <div class="conment_area padding_v_20 lineheight_2">
      <div class="black font_16">参与评论</div>
      <div :class="['border', 'circle_5', 'bg_light', {'flex_center': !login}, 'margin_b_15',]" style="height:160px">
        <textarea class="padding_15 all_height" v-if="$store.state.user_id" placeholder="请输入评论" v-model="inputComment"></textarea>
        <div v-if="!$store.state.user_id">
          <span class="padding_r_10 light">尚未登录，请登录后进行操作</span>
          <a class="block_inline text_center btn_blue width_100 padding_v_5" :href="'https://www.baoquan.com/sign-in?next=http://localhost:8080'+$route.path">立即登录</a>
        </div>
      </div>
      <div class="row">
        <button class="btn_blue pull_right width_100 padding_v_5" :disabled="!inputComment" v-if="$store.state.user_id" @click="doComment">评论</button>
      </div>
    </div>
  </div>
</template>

<script>
import ccontent from '@/components/Parts/Content'
import api from '@/api'
import format from '@/api/function'
export default {
  name: 'comment',
  data () {
    return {
      login: false,
      img: require('@/assets/images/detail/headimg_default.png'),
      perNum: 3,
      nowPage: 1,
      edit: -1,
      toWho: '',
      inputReply: '',
      inputComment: '',
      reply_normal: require('@/assets/images/detail/reply.png'),
      reply_disabled: require('@/assets/images/detail/reply_disabled.png'),
      newComment: {user_info: {mobile: '18367803500', head_logo: this.img}, content: '', created_at: format.date(), children_comment: []},
      newReply: {user_info: {mobile: '18367803500', head_logo: this.img}, content: '', created_at: format.date()},
      comments: [],
      data: []
    }
  },
  created () {
    console.log(this.$store.state)
    api.post('detail/project_comment', {sign: 'token=0&project_id=1'}).then(res => {
      console.log(res)
      this.comments = res
    })
  },
  computed: {
    commentData () {
      return this.comments.slice((this.nowPage - 1) * this.perNum, this.nowPage * this.perNum)
    }
  },
  methods: {
    openReply (to, n) {
      if (!this.$store.state.user_id) return false
      this.toWho = to
      this.edit = n
    },
    doComment () {
      var data = {token: '123123', project_id: this.$parent.project.id, project_name: encodeURIComponent(this.$parent.project.project_name), user_id: 'jzhthR2J29J5KVvXdLb91q', content: encodeURIComponent(this.edit === -1 ? this.inputComment : this.inputReply), parent_id: this.edit === -1 ? -1 : this.comments[this.edit].user_id}
      api.post('detail/save_comment', {sign: format.serialize(data)}).then(res => {
        console.log(res)
      })
      if (this.edit === -1) {
        this.comments.unshift(Object.assign({}, this.newComment, {content: this.inputComment}))
        this.inputComment = ''
        this.nowPage = 1
      } else {
        this.comments[this.edit].children_comment.push(Object.assign({}, this.newReply, {content: this.inputReply}))
        this.inputReply = ''
        this.edit = -1
      }
    },
    goPage (i) {
      if (!i || i > Math.ceil(this.comments.length / this.perNum)) return false
      this.nowPage = i
      this.edit = -1
    }
  },
  components: {
    ccontent
  }
}
</script>

<style type="text/css">
  .comment_list.padding_v_20:first-child{
    padding-top:0;
  }
  .comment_list.padding_v_20:last-child{
    border-bottom:0;
  }
  .pager_btn{
    display: inline-block;
    width:30px;
    border:1px solid #dbdee5;
    border-radius: 5px;
    text-align: center;
    cursor: pointer;
  }
  button[disabled]{
    opacity: 0.5
  }
  .pager_btn[disabled]{
    color: #ccc
  }
</style>



